<script>
	import { LayerCake, Svg } from 'layercake';
	import { scaleOrdinal } from 'd3-scale';

	import Marker from './Marker.svelte';
	import AxisX from './AxisX.svelte';

	export let data;
	export let height = 24;
	export let markers = true;
	export let axis = false;
	export let axisLabels = true;
	export let xKey = 'value';
	export let zKey = null;
	export let selected = null;
	export let hovered = null;
	export let padding = { top: 0, bottom: 0, left: 0, right: 10 };
	export let zDomain = null;
	export let colors = ["#206095", "#27A0CC", "#003C57", "#118C7B", "#A8BD3A", "#871A5B", "#F66068", "#746CB1", "#22D0B6"];
	
	function extent(data, key) {
		let filtered = data.map(d => d[key]);
		filtered.sort((a, b) => a - b);
		return [filtered[0], filtered[filtered.length - 1]];
	}
	
	data.forEach(d => {
		d[xKey] = +d[xKey];
	});

	const stroke = 'orange';
	const strokeWidth = 0;
	const formatTick = d => d.toFixed(0) + '%';

</script>

<style>
	.chart-container {
		width: 100%;
	}
</style>

<div class="chart-container" style="height: {height}px">
	<LayerCake
		padding={padding}
		x={xKey}
		z={zKey}
		xDomain={[0, 100]}
		yDomain={[0, 1]}
		{zDomain}
		zScale={scaleOrdinal()}
		zRange={colors}
		data={data}
	>
		<Svg>
			{#if markers}
			<Marker 
				bind:selected
				bind:hovered
			/>
			{/if}
			{#if axis}
			<AxisX
				ticks={4}
				solid={true}
				lineColor="rgba(255,255,255,0.3)"
				{formatTick}
				snapTicks={true}
				dyTick={0}
				flipped={true}
				{axisLabels}
			/>
			{/if}
		</Svg>
	</LayerCake>
</div>